.footer {
  border-radius: 30px 30px 0 0;
  background-color: $bg-color;
  padding: 72px 20px;

  &--blue {
    background-color: $bg2-color;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 80px;

    @include onNotebook {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, min-content);
    }
  }
}